import './style.css'
import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import * as dat from 'lil-gui'
import CANNON from 'cannon'
/**
 * Debug
 */
const gui = new dat.GUI()
const debugObject = {}


/**
 * Controls
 */
let moveForward = false;
let moveBackward = false;
let moveLeft = false;
let moveRight = false;
let canJump = false;
const onKeyDown = function (event) {

    switch (event.code) {

        case 'ArrowUp':
        case 'KeyW':
            moveForward = true;
            break;

        case 'ArrowLeft':
        case 'KeyA':
            moveLeft = true;
            break;

        case 'ArrowDown':
        case 'KeyS':
            moveBackward = true;
            break;

        case 'ArrowRight':
        case 'KeyD':
            moveRight = true;
            break;

        case 'Space':
            if (canJump === true) velocity.y += 350;
            canJump = false;
            break;

    }

};

const onKeyUp = function (event) {

    switch (event.code) {

        case 'ArrowUp':
        case 'KeyW':
            moveForward = false;
            break;

        case 'ArrowLeft':
        case 'KeyA':
            moveLeft = false;
            break;

        case 'ArrowDown':
        case 'KeyS':
            moveBackward = false;
            break;

        case 'ArrowRight':
        case 'KeyD':
            moveRight = false;
            break;

    }

};

document.addEventListener('keydown', onKeyDown);
document.addEventListener('keyup', onKeyUp);




/**
 * Base
 */
// Canvas
const canvas = document.querySelector('canvas.webgl')

// Scene
const scene = new THREE.Scene()

/**
 * Textures
 */
const textureLoader = new THREE.TextureLoader()
const cubeTextureLoader = new THREE.CubeTextureLoader()

const environmentMapTexture = cubeTextureLoader.load([
    '/textures/environmentMaps/4/nx.png',
    '/textures/environmentMaps/4/px.png',
    '/textures/environmentMaps/4/py.png',
    '/textures/environmentMaps/4/ny.png',
    '/textures/environmentMaps/4/pz.png',
    '/textures/environmentMaps/4/nz.png'
])
/**
 * Physics
 */
// world
const world = new CANNON.World()
// Collision optimization may generate bugs 
world.broadphase = new CANNON.SAPBroadphase(world)
// Ignore stand still objects
world.allowSleep = true
world.gravity.set(0, -9.82, 0)
// Materials
const concreteMaterial = new CANNON.Material("Concrete")
const plasticMaterial = new CANNON.Material("plastic")

const conretePlasticContactMaterial = new CANNON.ContactMaterial(
    concreteMaterial,
    plasticMaterial,
    {
        friction: 0.006,
        restitution: 0.0
    }
)
world.addContactMaterial(conretePlasticContactMaterial)
world.defaultContactMaterial = conretePlasticContactMaterial

// Floor
const floorShape = new CANNON.Plane()
const floorBody = new CANNON.Body()
floorBody.material = concreteMaterial
floorBody.mass = 0
floorBody.addShape(floorShape)
floorBody.quaternion.setFromAxisAngle(
    new CANNON.Vec3(-1, 0, 0),
    Math.PI / 2
)
world.addBody(floorBody)

/**
 * Floor
 */
const floor = new THREE.Mesh(
    new THREE.PlaneGeometry(10, 10),
    new THREE.MeshStandardMaterial({
        color: '#777777',
        metalness: 0.5,
        roughness: 0.4,
        envMap: environmentMapTexture,
        envMapIntensity: 0.5
    })
)
floor.receiveShadow = true
floor.rotation.x = - Math.PI * 0.5
scene.add(floor)

/**
 * Lights
 */
const ambientLight = new THREE.AmbientLight(0xffffff, 0.7)
scene.add(ambientLight)

const directionalLight = new THREE.DirectionalLight(0xffffff, 0.2)
directionalLight.castShadow = true
directionalLight.shadow.mapSize.set(1024, 1024)
directionalLight.shadow.camera.far = 15
directionalLight.shadow.camera.left = - 7
directionalLight.shadow.camera.top = 7
directionalLight.shadow.camera.right = 7
directionalLight.shadow.camera.bottom = - 7
directionalLight.position.set(5, 5, 5)
scene.add(directionalLight)

/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

window.addEventListener('resize', () => {
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

/**
 * Camera
 */

// Base camera
const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height, 0.1, 100)

// Camera Controls
// const controls = new OrbitControls(camera, canvas)
// controls.enableDamping = true


camera.position.set(- 3, 2, 10)
scene.add(camera)



/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas
})
renderer.shadowMap.enabled = true
renderer.shadowMap.type = THREE.PCFSoftShadowMap
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))

/**
 * Sounds
 */
const audioListener = new THREE.AudioListener();

camera.add(audioListener);

const audioLoader = new THREE.AudioLoader();



const playHitSound = (collision) => {
    const impactStrength = collision.contact.getImpactVelocityAlongNormal()
    let collidingObj = objectsToUpdate.find(e => e.body.id === collision.contact.bi.id)

    if (impactStrength > 0.2) {

        collidingObj.mesh.children[0].setVolume(impactStrength / 6)
        collidingObj.mesh.children[0].play()
    }
}

/**
 * Player
 */

var players = []
const createPlayer = () => {

    const playerStartPos = {
        x: 0,
        y: 3,
        z: 0
    }

    const raycaster = new THREE.Raycaster();
    raycaster.near = 0.49
    raycaster.far = 0.50
    const playerMesh = new THREE.Mesh(
        new THREE.BoxGeometry(1, 1, 1),
        new THREE.MeshStandardMaterial({
            metalness: 0.3,
            roughness: 0.1,
            envMap: environmentMapTexture,
            envMapIntensity: 0.8

        })
    )
    playerMesh.castShadow = true
    playerMesh.position.copy(playerStartPos)
    scene.add(playerMesh)

    const shape = new CANNON.Box(new CANNON.Vec3(0.5, 0.5, 0.5))

    const body = new CANNON.Body({
        mass: 1,
        position: new CANNON.Vec3(playerStartPos),
        shape: shape,
        material: plasticMaterial
    })
    body.position.copy(playerStartPos)
    body.angularDamping = 1
    body.allowSleep = false
    world.addBody(body)
    objectsToUpdate.push({
        mesh: playerMesh,
        body: body,
    })
    players.push({
        mesh: playerMesh,
        body: body,
        raycaster: raycaster,
    })
}


/**
 * Utils
 */
const objectsToUpdate = []
const createSphere = (radius, position) => {
    // Three.js mesh
    audioLoader.load('/sounds/hit.mp3', function (buffer) {
        // hitSound.setBuffer(audioBuffer);

        const mesh = new THREE.Mesh(
            new THREE.SphereGeometry(radius, 20, 20),
            new THREE.MeshStandardMaterial({
                metalness: 0.3,
                roughness: 0.1,
                envMap: environmentMapTexture,
                envMapIntensity: 0.8
            })
        )
        mesh.castShadow = true
        mesh.position.copy(position)
        scene.add(mesh)

        const audio = new THREE.PositionalAudio(audioListener);
        audio.setBuffer(buffer);
        audio.setRefDistance(3)
        mesh.add(audio)

        const shape = new CANNON.Sphere(radius)

        const body = new CANNON.Body({
            mass: 1,
            position: new CANNON.Vec3(0, 3, 0),
            shape: shape,
            material: plasticMaterial
        })
        body.position.copy(position)
        body.addEventListener('collide', playHitSound)
        world.addBody(body)

        objectsToUpdate.push({
            mesh: mesh,
            body: body,
            audio: audio
        })
        body.position.z = 0
        body.fixedRotation = true
    },)

}



debugObject.createSphere = () => {
    createSphere(
        Math.random() * 0.5 + 0.2,
        {
            x: (Math.random() - 0.5) * 3,
            y: 3,
            z: 0
        }
    )
}
gui.add(debugObject, 'createSphere')

createPlayer()

class GameSettings {
    constructor(playerForce, playerMaxVelocity, gravity, playerJumpForce) {
        this.playerForce = playerForce
        this.playerMaxVelocity = playerMaxVelocity
        this.gravity = gravity
        this.playerJumpForce = playerJumpForce
    }
}

var gameSettings = new GameSettings(90, 8, -35, 850)
gui.add(gameSettings, "playerForce", 10, 200)
gui.add(gameSettings, "playerMaxVelocity", 1, 30)
gui.add(gameSettings, "gravity", -50, -9.8)
gui.add(gameSettings, "playerJumpForce", 200, 1000)
gui.add(players[0].raycaster, "far", 0, 5)


gui.add(conretePlasticContactMaterial, "friction", 0, 0.01)
console.log(players[0].raycaster)
console.log(floor.position)
/**
 * Animate
 */
const clock = new THREE.Clock()
let oldElapsedTime = 0
var LastJumpTIme = 0
const tick = () => {

    const elapsedTime = clock.getElapsedTime()
    const deltaTime = elapsedTime - oldElapsedTime
    oldElapsedTime = elapsedTime
    world.gravity.set(0, gameSettings.gravity, 0)
    // update physics World
    world.step(1 / 60, deltaTime, 3)


    for (const object of objectsToUpdate) {
        object.body.position.z = 0
        object.mesh.position.copy(object.body.position)
        object.mesh.quaternion.copy(object.body.quaternion)
    }

    if (players[0].body.velocity.x > gameSettings.playerMaxVelocity || players[0].body.velocity.x < - gameSettings.playerMaxVelocity) {
        players[0].body.velocity.x = (players[0].body.velocity.x / Math.abs(players[0].body.velocity.x)) * gameSettings.playerMaxVelocity
    }
    if (moveRight) {
        players[0].body.force.set(gameSettings.playerForce, 0, 0)
    }
    if (moveLeft) {
        players[0].body.force.set(-gameSettings.playerForce, 0, 0)
    }
    if (moveForward && players[0].raycaster.intersectObjects([floor])[0] && (elapsedTime - LastJumpTIme > 0.05)) {
        LastJumpTIme = elapsedTime
        console.log(players[0].raycaster.intersectObjects([floor])[0])
        players[0].body.force.set(0, gameSettings.playerJumpForce, 0)
    }
    camera.position.x = players[0].body.position.x
    players[0].raycaster.set(players[0].body.position, new THREE.Vector3(0, -1, 0))

    // Render
    renderer.render(scene, camera)

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()